/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.to-director-modal.ui.modal {
  border-radius: 15px;
}
.to-director-modal.ui.modal .header,
.to-director-modal.ui.modal .actions,
.to-director-modal.ui.modal:first-child {
  border-radius: 15px;
}
.to-director-modal.ui.modal .content {
  padding: 0;
  border-radius: 15px !important;
}
.to-director-modal.ui.modal .description {
  padding: 1rem 2.222rem;
}
.to-director-modal__fields {
  margin-bottom: 1rem;
}
.to-director-modal__fields__input {
  display: flex;
  border-bottom: 2px solid #EEEEEE;
  color: #ADADAD;
  align-items: center;
}
.to-director-modal__fields__input p {
  margin: 0;
  padding: 0;
}
.to-director-modal__fields__input p:first-child {
  width: 3.888rem;
  padding: 0.5rem 1.9444rem 0.5rem 1.3888rem;
}
.to-director-modal__fields__input p:nth-child(2) {
  margin-left: 1.9444rem;
}
.to-director-modal__fields__input__subject {
  color: black;
}
.to-director-modal .ui.form textarea {
  border: 0;
  margin-left: 0.5rem;
}
.to-director-modal__email-footer-icons {
  width: 15.5555rem;
  float: left;
  margin-left: 10px;
}
